/******************************************************************
VARIABLES - EFFECTS
******************************************************************/

// Shadows
$shadow01:              0 1px 1px rgba($color05, 1);
$shadow02:              0 8px 40px rgba($color01, 0.08);


// Beziers
$bezier01:              cubic-bezier(.12,.79,.69,.9);


// Borders
$border-radius:         20px;
$border-color:          rgba($color-black, 0.1);


// Transitions
$trans-time:            0.28s;
$trans-type:            $bezier01;

$trans-time-sm:         0.14s;
$trans-time-md:         0.42s;
$trans-time-lg:         0.68s;

$transition-default:    all $trans-time $trans-type;