/******************************************************************
EDITOR
Styles for WordPress TinyMCE editor content output.
Always use a container with .editor-content class to output editor content.
******************************************************************/


.editor-content, .block-editor {
	line-height: $line-height-base;

	> *:first-child {
		margin-top: 0;
	}
	> *:last-child {
		margin-bottom: 0;
	}

	H1, H2, H3, H4, H5, H6 {
		color: $color-text;
		margin: 24px 0;
	}

	H1, H2 {
		font-size: $font-size-h2;
		line-height: $line-height-sm;
	}

	P {
		margin: 24px 0;
	}

	A {
		@extend .has-link-color-black;
		text-decoration: underline;
	}

	STRONG {
		font-weight: $font-weight-bold;
	}

	UL, OL {
		margin: 2px 0 24px 0;
		padding-left: 0;
	}

	UL {
		list-style-type: none;

		LI {
			position: relative;
			padding-left: 42px;
			margin-bottom: 8px;

			&:before {
				content: "";
				display: inline-block;
				position: absolute;
				left: 20px;
				top: 11px;
				height: 7px;
				width: 7px;
				border-radius: 4px;
				background-color: $color-black;
			}
		}
	}

	OL {
		list-style-position: inside;

		LI {
			position: relative;
			padding-left: 20px;
		}
	}

	P + UL, P + OL {
		margin-top: -8px;
	}

	IMG {
		max-width: 100%;
		height: auto;
		margin-bottom: 30px;

		&.alignleft {
			display: inline;
			float: left;
			margin-right: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	
		&.alignright {
			display: inline;
			float: right;
			margin-left: 30px;
	
			@include below-mobile {
				display: block;
				float: none;
				margin: 0 auto;
			}
		}
	}

	BLOCKQUOTE {
		position: relative;
		appearance: none;
		margin: 48px 0;
		padding-left: 64px;

		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 42px;
			height: 35px;
			background: url('../../assets/icons/icon-quote.svg');
			background-repeat: no-repeat;
			background-size: cover;
			background-position: 50% 50%;
			z-index: 0;
			opacity: 0.85;
		}

		CITE {
			display: block;
			margin-top: 16px;
			font-style: italic;
			color: $color-gray-light;
		}

		P {
			margin: 0;
		}
	}

    .wp-caption {
		width: 100%;
		max-width: 300px;
		height: auto;
		margin-bottom: 20px;
		line-height: 1.2;

		IMG {
			margin-bottom: 8px;
		}
		&.alignright {
			float: right;
			padding-left: 10px;
		}
		&.alignleft {
			float: left;
			padding-right: 10px;
		}
		&.aligncenter {
			margin: 0 auto;
		}
	}
	.wp-caption-text {
		//font-style: italic;
		color: #aaa;
		margin-top: 0;
		text-decoration: none !important;
	}

}