/******************************************************************
GLOBAL BASE STYLES
Opinionated base styles to start building upon
******************************************************************/


*, *:before, *:after {
    box-sizing: border-box;
}

::selection {
    color: $color-selection;
    background: $color-selection-bg;
}

::placeholder {
    color: $color-placeholder !important;
}

HTML {
    scroll-behavior: smooth;
    font-family: $font-family-main;
    font-size: $font-size-calc-base;
    font-weight: $font-weight-normal;
    line-height: $line-height-base;
    color: $color-text;
    -webkit-font-smoothing: antialiased;
}

BODY {
    font-size: $font-size-p;
    background: $color04;

    @include below-desktop {
        font-size: 1.6rem;
    }

    @include below-mobile {
        font-size: 1.4rem;
    }
}

MAIN {
    overflow: hidden;

    @include below-desktop {
        padding-top: 120px;
    }
}

P {
    margin: 0 0 24px 0;
}

H1, H2, H3, H4, H5, H6, .h1, .h2, .h3, .h4, .h5 {
    font-weight: $font-weight-bold;
    font-family: $font-family-headings;
    margin-top: 0;
}
H1, .h1 {
    font-size: $font-size-h1;

    @include below-mobile {
        font-size: $font-size-h2;
    }
}
H2, .h2 {
    font-size: $font-size-h2;

    @include below-mobile {
        font-size: $font-size-h3;
    }
}
H3, .h3 {
    font-size: $font-size-h3;
}
H4, .h4 {
    font-size: $font-size-h4;
}
H5, .h5 {
    font-size: $font-size-h5;
}
H1, .h1, H2, .h2, H3, .h3 {
    font-weight: $font-weight-black;
    line-height: $line-height-sm;
}

A {
    text-decoration: none;
    color: $color-link;
    transition: color $trans-time $trans-type;

    &:hover {
        color: $color-hover;
        transition: color $trans-time $trans-type;
    }
}

BUTTON {
	appearance: none;
	display: inline-block;
	position: relative;
	outline: 0;
	border: 0;
	border-radius: 0;
	background: 0;
	text-decoration: none;
	color: $color-link;
	transition: color $trans-time $trans-type;

	&:hover {
		color: $color-hover;
		transition: color $trans-time $trans-type;
		cursor: pointer;
	}

	&:active, &:focus {
		outline: 0;
	}
}

IMG {
    display: block;
}

UL {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

FIGURE {
    margin: 0 0 24px 0;
}

FIELDSET {
    padding: 0;
    margin: 0;
    border: 0;
}

LEGEND {
    margin-bottom: 24px;
}

ABBR[title] {
    text-decoration: none;
}

HR {
    box-sizing: border-box;
    border: none;
}

TABLE {
    width: 100%;
    border-collapse: collapse;

    TH, TD {
        text-align: left;
        border: 1px solid $color-gray;
        padding: 16px 20px;
        background: $color-white;

        &:not(:last-child) {
            border-right: none;
        }
    }

    TH {
        background: $color-gray-lightest;
    }
}

BLOCKQUOTE {
    position: relative;
    appearance: none;
    margin: 0;
}

CODE {
    display: block;
    width: 100%;
    padding: 16px 24px;
    background: $color-gray-light;
}