/******************************************************************
VARIABLES - TYPOGRAPHY
******************************************************************/

// Fonts
$font-family-main:             Lato, Montserrat, Helvetica, Arial, sans-serif;
$font-family-headings:         Lato, Montserrat, Helvetica, Arial, sans-serif;
$font-family-accents:          Lato, Montserrat, Helvetica, Arial, sans-serif;


// Sizes
$font-size-calc-base:   62.5%;
$font-size-base:        1.8rem;

$font-size-xxl:         4.8rem;
$font-size-xl:          3.2rem;
$font-size-lg:          2.4rem;
$font-size-md:          2.1rem;
$font-size-sm:          1.6rem;
$font-size-xs:          1.2rem;

$font-size-h1:          $font-size-xxl;
$font-size-h2:          $font-size-xl;
$font-size-h3:          $font-size-lg;
$font-size-h4:          $font-size-md;
$font-size-h5:          $font-size-sm;
$font-size-lead:        2.0rem;
$font-size-p:           $font-size-base;


// Line heights
$line-height-base:      1.6;
$line-height-md:        1.48;
$line-height-sm:        1.2;
$line-height-lg:        2.0;


// Letter spacing
$letter-spacing-base:   normal;


// Weights
$font-weight-light:     300;
$font-weight-normal:    400;
$font-weight-medium:    500;
$font-weight-semibold:  600;
$font-weight-bold:      700;
$font-weight-black:     900;

$font-weight-base:      $font-weight-normal;