/******************************************************************
GLOBAL MODIFIERS
Various global modifiers to be used throughout the project
Always prefix with "has-"
******************************************************************/


.has-no-scroll {
    overflow: hidden;
}

.has-no-scroll-x {
    overflow-x: hidden;
}

.has-no-scroll-y {
    overflow-y: hidden;
}

.has-no-padding-left {
    padding-left: 0px;
}

.has-no-padding-right {
    padding-right: 0px;
}

.has-no-padding-top {
    padding-top: 0px;
}

.has-no-padding-bottom {
    padding-bottom: 0px;
}

.has-no-padding-horizontal {
    padding-left: 0px;
    padding-right: 0px;
}

.has-no-padding-vertical {
    padding-top: 0px;
    padding-bottom: 0px;
}

.has-no-margin-left {
    padding-left: 0px;
}

.has-no-margin-right {
    padding-right: 0px;
}

.has-no-margin-top {
    padding-top: 0px;
}

.has-no-margin-bottom {
    padding-bottom: 0px;
}

.has-no-margin-horizontal {
    margin-left: 0px;
    margin-right: 0px;
}

.has-no-margin-vertical {
    margin-top: 0px;
    margin-bottom: 0px;
}

.has-text-align-center {
    text-align: center;
}

.has-text-align-left {
    text-align: left;
}

.has-text-align-right {
    text-align: right;
}

.has-cursive-text {
    font-style: italic;
}

.has-bold-text {
    font-weight: $font-weight-bold;
}

.has-highlighted-text {
    color: $color02;
}

.has-hidden-text {
    font: 0/0 a;
    color: transparent;
    text-shadow: none;
    background-color: transparent;
    border: 0;
}

.has-animated-icon-left {
    &:hover {
        .icon {
            animation-name: buttonIconBounceLeft;
            animation-duration: 0.64s;
            animation-direction: forwards;
        }
    }
}
.has-animated-icon-right {
    &:hover {
        .icon {
            animation-name: buttonIconBounceRight;
            animation-duration: 0.64s;
            animation-direction: forwards;
        }
    }
}

.has-black-links {
    A {
        color: $color-black;

        &:hover {
            color: rgba($color-black, $opacity-lg);
        }
    }
}

.has-white-links {
    A {
        color: $color-white;

        &:hover {
            color: rgba($color-white, $opacity-md);
        }
    }
}

.has-link-color-black {
    color: $color-black;

    &:hover {
        color: rgba($color-black, $opacity-lg);
    }
}

.has-link-color-white {
    color: $color-white;

    &:hover {
        color: rgba($color-white, $opacity-md);
    }
}

.has-underline {
    text-decoration: underline;
}