/******************************************************************
VARIABLES - COLORS
******************************************************************/

// Theme colors
$color01:                   #0048AA;
$color02:                   #F5873E;
$color03:                   #008BD0;
$color04:                   #ECF3F8;
$color05:                   #C8D9EE;


// Gradients
$gradient01:                linear-gradient(0deg, $color01 0%, $color02 100%);


// Grayscale
$color-black:               #131313;
$color-white:               #fff;

$color-gray-lightest:       #eee;
$color-gray-lighter:        #ccc;
$color-gray-light:          #aaa;
$color-gray:                #666;
$color-gray-dark:           #444;
$color-gray-darker:         #333;
$color-gray-darkest:        #222;


// State & alert colors
$color-positive:            #2AC769;
$color-positive-dark:       #0C4735;
$color-negative:            #C5014C;
$color-negative-dark:       #8e0037;
$color-warning:             #F6A609;
$color-warning-dark:        #3671a0;
$color-neutral:             $color01;
$color-disabled:            $color-gray-light;
$color-placeholder:         #D4D4D6;


// Opacities
$opacity-sm:                0.75;
$opacity-md:                0.6;
$opacity-lg:                0.25;


// Base element colors
$color-text:                $color-gray-dark;
$color-heading:             $color02;
$color-link:                $color03;
$color-hover:               rgba($color-link, $opacity-md);
$color-visited:             rgba($color-link, $opacity-md);

$color-selection:           $color-white;
$color-selection-bg:        $color01;