/******************************************************************
BUTTON STYLES
All button styles, starts with a default baseline
******************************************************************/


.button {
	display: inline-block;
	padding: 16px 36px;
	min-width: 160px;
	min-height: 60px;
	background-color: $color01;
	color: $color-white;
	border: 1px solid $color01;
	font-size: $font-size-sm;
	text-decoration: none;
	border-radius: 60px;
	white-space: nowrap;
	transition: $transition-default;
	cursor: pointer;

	&:hover, &:focus {
		color: $color-white;
		background-color: $color02;
		border-color: $color02;
		transition: $transition-default;
	}

	@include below-mobile-sm {
		width: 100%;
		max-width: 100%;
		min-width: 200px;
		text-align: center;
	}

	// Main variations
	&--bordered {
		background-color: transparent;
		color: $color01;
		border-color: $color01;

		&:hover, &:focus {
			color: $color-white;
			background-color: $color01;
			border-color: $color01;
		}
	}

	// Color variations
	&--color-secondary {
		color: $color-white;
		background-color: $color02;
		border-color: transparent;

		&:hover, &:focus {
			color: $color-white;
			background-color: $color01;
			border-color: transparent;
		}
	}
	&--color-secondary#{&}--bordered {
		color: $color02;
		background-color: transparent;
		border-color: $color02;

		&:hover, &:focus {
			color: $color-white;
			background-color: $color02;
			border-color: $color02;
		}
	}

	// Size variations
	&--size-sm {
		padding: 8px 24px;
	}

	// Icon variations
	&--icon-left, &--icon-right {
		position: relative;

		.icon {
			width: 22px;
			height: 22px;
			position: absolute;
			top: calc(50% - 11px);
		}
	}
	&--icon-left {
		padding-left: 84px;

		.icon {
			left: 36px;
		}
	}
	&--icon-right {
		padding-right: 84px;

		.icon {
			right: 36px;
		}
	}
	&--icon-only {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		min-width: 56px;
		min-height: 56px;
		padding: 5px;


		.icon {
			width: 16px;
			height: 16px;
			margin-bottom: 1px;
		}
		font-size: 0;
	}
	&--icon-mobile-only {
		.icon {
			display: none;
		}

		@include below-mobile {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 56px;
			height: 56px;
			min-width: auto;
			padding: 0;

			.icon {
				display: block;
				width: 16px;
				height: 16px;
				margin-bottom: 1px;
			}
			font-size: 0;
		}
	}

	// States
	&.is-disabled {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: default;
	}
	&.is-loading {
		opacity: $opacity-md;
		pointer-events: none;
		cursor: wait;
	}
}

.more-link {
	position: relative;
	display: inline-block;
	font-weight: $font-weight-bold;
	text-decoration: none;
	padding: 3px 36px 3px 0;
	white-space: nowrap;
	line-height: 1;
	color: $color02;
	
	.icon {
		width: 20px;
		height: 20px;
		position: absolute;
		right: 0;
		top: calc(50% - 10px);
	}

	&:hover {
		.icon {
			animation-name: buttonIconBounceRight;
            animation-duration: $trans-time-md;
            animation-direction: forwards;
		}
	}

	@include below-mobile {

	}
}